@use 'base/variables' as v;
@use 'base/mixins' as mix;

.filtros {
    background-color: v.$blanco;
    margin-top: 2.5rem;
    padding: 2rem;
    border-radius: 1rem;
    .filtros-inputs{
        @include mix.tablet {
            display: flex;
            gap: 2rem;

        }
        
        .campo {
            display: flex;
            justify-content: space-between;
            gap: 1rem;
            margin-bottom: 2rem;
            font-size: 1.4rem;
            font-weight: 700;
            
            &:last-of-type {
                margin-bottom: 0;
            }
            @include mix.tablet {
                margin: 0;    
                align-items: center;
                justify-content: center;
            }
        }

        input[type = 'radio'] {
            accent-color: v.$indigo;
            width: 1.6rem;
            height: 1.6rem;
        }

        h2 {
            color: v.$indigo;
            margin: 0;
            font-size: 2rem;
            margin-bottom: 2rem;

            @include mix.tablet {
                margin-bottom: 0;
            }
        }
    }
}