@use 'base/variables' as v;
@use 'base/mixins' as mix;

.dashboard {
    background-color: v.$grisClaro;

    @include mix.tablet{
        display: flex;
        height: 100vh;
    }

    .principal {
        flex: 1;
        overflow: scroll;
    }

    .formularios {
        input:not([type='submit']) {
            background-color: v.$blanco;
        }
        input[type='submit'] {
            @include mix.boton(v.$indigo,v.$blanco);
            padding: 1rem;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 1.4rem;

        }
    }
}

.contenido {
    padding: 2rem;
    .nombre-pagina {
        font-size: 2.8rem;
        text-align: center;
    }
}

