@use 'base/variables' as v;
@use 'base/mixins' as mix;

.olvide {
    margin-top: 5rem;
    
    .uptask {
        @include mix.textoDegradado(v.$rosa);
    }

    input[type='submit'] {
        @include mix.boton(v.$rosa,v.$blanco);
    }

    .acciones {
        a{
            color: v.$rosa;
        }
    }

}