@use 'base/variables' as v;
@use 'base/mixins' as mix;

.login {
    .uptask {
        @include mix.textoDegradado(v.$cyan);
    }

    input[type='submit'] {
        @include mix.boton(v.$cyan, v.$blanco);
    }

    .acciones{
        a{
            color: v.$cyan;
        }
    }   
}
