@use 'base/variables' as v;
@use 'base/mixins' as mix;

.barra {
    background-color: v.$blanco;   
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px rgba(0,0,0, .4); 
    @include mix.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    p{
        margin: 0;
        font-size: 1.4rem;

        span{
            font-weight: 700;
            color: v.$indigo;
        }
    }

    .cerrar-sesion {
        @include mix.boton(darken(v.$indigo,20%), v.$blanco);
        padding: 1rem;
        font-size: 1.2rem;
        border-radius: .5rem;
    }
}