@use 'base/variables' as v;

.alerta {
    padding: 1rem 1rem 1rem 2rem;
    font-weight: 900;
    font-size: 1.4rem;
    margin: 1rem 0;
    max-width: 80rem;
    text-transform: uppercase;

    &.error {
        background-color: lighten(v.$rojo, 60%);
        color: v.$rojo;
        border-left: .5rem solid ;
    }

    &.exito {
        background-color: lighten(v.$verde, 60%);
        color: v.$verde;
        border-left: .5rem solid ;
    }

}