@use 'base/variables' as v;
@use 'base/mixins' as mix;

.modal{
    position: absolute;
    background-color: rgb(0 0 0 / .8);
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    .formularios {
        max-width: 60rem;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0 auto;
        transform: translateY(-30rem);
        transition: all 300ms ease-in-out;
        opacity: 0;
        border-radius: 1rem;

        input[type='text'] {
            border: 1px solid v.$gris;
        }

        &.animar {            
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }

        &.cerrar {
            transform: translateY(-30rem);
            opacity: 0;
        }

        legend{
            font-size: 2rem;
            margin-bottom: 2rem;
            text-align: center;
            font-weight: bold;
            display: block;
        }

        .opciones {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }
        .submit-nueva-tarea{
            @include mix.boton(v.$indigo,v.$blanco);
        }

        .cerrar-modal{
            @include mix.boton(v.$naranja,v.$blanco);
        }
    }
}