@use 'base/variables' as v;
@use 'base/mixins' as mix;

.mensaje {
    margin-top: 5rem;

    .uptask {
        @include mix.textoDegradado(v.$naranja);
    }

    input[type='submit'] {
        @include mix.boton(v.$naranja, v.$blanco);
    }

    .acciones{
        a{
            color: v.$naranja;
        }
    }   
}