@use 'base/variables' as v;
@use 'base/mixins' as mix;

.restablecer {
    margin-top: 5rem;
    
    .uptask {
        @include mix.textoDegradado(v.$azul);
    }

    input[type='submit'] {
        @include mix.boton(v.$azul,v.$blanco);
    }

    .acciones {
        a{
            color: v.$azul;
        }
    }

}