@use 'base/variables' as v;
@use 'base/mixins' as mix;

.no-proyectos {
    a{
        color: v.$indigo;
    }
}

.listado-proyectos {
    list-style: none;
    padding: 0;
    margin-top: 3rem;

    @include mix.grid(1, 2rem);
    
    @include mix.tablet {
        @include mix.grid(2, 2rem);
    }
    
    @include mix.desktop {
        @include mix.grid(3, 2rem);
    }

    a{
        color: v.$blanco;
    }

    .proyecto {
        background-color: lighten(v.$indigo, 5%);
        min-height: 12rem;
        display: flex;
        align-items: center;
        padding: 2rem;
        line-height: 2;
        border-radius: .5rem;

        &:hover{
            background-color: darken(v.$indigo, 5%);
        }  
        a{
            color: v.$blanco;
        }
    }
}