@use 'base/variables' as v;
@use 'base/mixins' as mix;

.crear {
    margin-top: 5rem;

    .uptask {
        @include mix.textoDegradado(v.$morado);
    }

    input[type='submit'] {
        @include mix.boton(v.$morado, v.$blanco);
    }

    .acciones{
        a{
            color: v.$morado;
        }
    }   
}