@use 'base/variables' as v;
@use 'base/mixins' as mix;

.sidebar {
    background-color: v.$indigo;
    padding: 2rem;

    h2 {
        color: v.$blanco;
        margin: 0;
        text-align: center;
    }

    .sidebar-nav {
        margin-top: 5rem;  
         
        a{
            color: v.$blanco;
            display: block;
            margin-bottom: 1rem;
            padding: 1rem;
            transition: background-color 300ms;
            text-transform: uppercase;
            font-size: 1.2rem;
            font-weight: bold;

            &:hover,
            &.activo {
                background-color: darken(v.$indigo, 20%);
                border-radius: .5rem;
            }
        }
    }
}