@use 'base/variables' as v;
@use 'base/mixins' as mix;

.contenedor-nueva-tarea {
    display: flex;
    justify-content: center;
    .agregar-tarea{
        @include mix.boton(v.$cyan,v.$blanco);
    }
}

.listado-tareas{
    list-style: none;
    margin-top: 5rem;
    padding: 0;

    .no-tareas{
        text-align: center;
    }
}

.tarea {
    padding: .5rem 2rem;
    border-bottom: 1px solid darken(v.$gris, 5%);
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &:last-of-type {
        border: none;
    }

    p {
        font-size: 1.4rem;
    }

    .opciones {
        display: flex;
        gap: 1rem;
        button {
            border: none;
            font-size: .9rem;
            padding: .5rem 1rem;
            border-radius: 1rem;
            text-transform: uppercase;
            font-weight: 700;
    
            &:hover {
                cursor: pointer;
            }
        }
    
        .completa {
            background-color: v.$azul;
            color: v.$blanco;
        }
        .pendiente {
            background-color: v.$naranja;
            color: v.$blanco;
        }
        .eliminar-tarea{
            background-color: v.$rojo;
            color: v.$blanco;
        }
    }

}